let burger = document.querySelector(".burger")
let body = document.querySelector("body")
let header = document.querySelector(".header")
let navBackdrop = document.querySelector(".nav__backdrop")

if(burger) {
    burger.onclick = function () {
        header.classList.toggle("_open")
    }
}

if(navBackdrop) {
    navBackdrop.onclick = function () {
        header.classList.remove("_open")
    }
}


let headerMenu = document.querySelector(".header-menu")
let headerMenuToggle = document.querySelector(".header-menu__show")
let headerMenuBackdrop = document.querySelector(".header-menu__backdrop")


if(headerMenuToggle){
    headerMenuToggle.onclick = function () {
        headerMenu.classList.toggle("_open")
    }

    headerMenuBackdrop.onclick = function () {
        headerMenu.classList.remove("_open")
    }
}
